import './Accordion.css';
import Col from 'react-bootstrap/Col';
import Nav from 'react-bootstrap/Nav';
import Row from 'react-bootstrap/Row';
import Tab from 'react-bootstrap/Tab';
import firstimg from './img/first (2).png';
import secondimg from './img/second.webp';
import thirdimg from './img/third (2).png';
import fourthimg from './img/fourth.webp';
import fifthimg from './img/fifth.webp';
import Accordion from 'react-bootstrap/Accordion';

function AccordionSlider() {
    return (
        <>
            <div className='AccordionSlider'>
                <div className='AccordionSlider-con'>
                    <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                        <Row className='acc'>
                            <Col sm={3} className='acchh'>
                                <Accordion defaultActiveKey={['0']} alwaysOpen>
                                    <Nav variant="pills" className="flex-column">
                                        <Accordion.Item eventKey="0">
                                            <Nav.Item>
                                                <Nav.Link eventKey="first"> <Accordion.Header>Blogs  </Accordion.Header></Nav.Link>
                                                <Accordion.Body>
                                                    Build personal and professional blogs with Astro's built-in Markdown support and content APIs.                </Accordion.Body>
                                            </Nav.Item>
                                        </Accordion.Item>
                                        <Accordion.Item eventKey="1">
                                            <Nav.Item>
                                                <Nav.Link eventKey="second"> <Accordion.Header>Marketing  </Accordion.Header></Nav.Link>
                                                <Accordion.Body>
                                                    Build personal and professional blogs with Astro's built-in Markdown support and content APIs.                </Accordion.Body>
                                            </Nav.Item>
                                        </Accordion.Item>
                                        <Accordion.Item eventKey="2">
                                            <Nav.Item>
                                                <Nav.Link eventKey="third"> <Accordion.Header>Agencies  </Accordion.Header></Nav.Link>
                                                <Accordion.Body>
                                                    Build personal and professional blogs with Astro's built-in Markdown support and content APIs.                </Accordion.Body>
                                            </Nav.Item>
                                        </Accordion.Item>
                                        <Accordion.Item eventKey="3">
                                            <Nav.Item>
                                                <Nav.Link eventKey="fourth"> <Accordion.Header>E-Commerce  </Accordion.Header></Nav.Link>
                                                <Accordion.Body>
                                                    Build personal and professional blogs with Astro's built-in Markdown support and content APIs.                </Accordion.Body>
                                            </Nav.Item>
                                        </Accordion.Item>
                                        <Accordion.Item eventKey="4">
                                            <Nav.Item>
                                                <Nav.Link eventKey="fifth"> <Accordion.Header>Portfolios  </Accordion.Header></Nav.Link>
                                                <Accordion.Body>
                                                    Build personal and professional blogs with Astro's built-in Markdown support and content APIs.                </Accordion.Body>
                                            </Nav.Item>
                                        </Accordion.Item>

                                    </Nav>

                                </Accordion>
                            </Col>
                            <Col sm={9} className='text-center'>
                                <Tab.Content>
                                    <Tab.Pane eventKey="first">
                                        <div> <img src={firstimg} alt='img-react' /></div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="second">
                                        <div> <img src={secondimg} alt='img-react' /></div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="third">
                                        <div> <img src={thirdimg} alt='img-react' /></div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="fourth">
                                        <div> <img src={fourthimg} alt='img-react' /></div>
                                    </Tab.Pane>
                                    <Tab.Pane eventKey="fifth">
                                        <div> <img src={fifthimg} alt='img-react' /></div>
                                    </Tab.Pane>
                                </Tab.Content>
                            </Col>
                        </Row>
                    </Tab.Container>
                </div>
            </div>
        </>
    );
}

export default AccordionSlider;