import './Benner.css';
import Google from './img/google.png'
function Benner() {
    return (
        <>
            <div className="main ">
                <div className="mx-auto">
                    <h1 className="mainHead text-white">Build the web you want</h1>
                    <p className="mainPara text-white">Astro is the all-in-one web framework designed for speed. Pull your content from anywhere and deploy everywhere, all powered by your favorite UI components and libraries.</p>
                    <button className="mx-auto d-flex btn btn-light px-5 py-3 rounded-pill">Get Started</button>
                </div>
            </div>
            <div className=" row container mx-auto">
               
                <div className="col-md-2 Benner-text ">Used by over 100,000 developers and teams around the world:</div>
                <div className='col-md-10'>
                <div className="row BENNER-MAIN">
                    <div className='col-md-2 BENNER-MAIN-1'><img src={Google} className="Benner-logo" alt="logo" /></div>
                    <div className='col-md-2 BENNER-MAIN-1'><img src={Google} className="Benner-logo" alt="logo" /></div>
                    <div className='col-md-2 BENNER-MAIN-1'><img src={Google} className="Benner-logo" alt="logo" /></div>
                    <div className='col-md-2 BENNER-MAIN-1 '><img src={Google} className="Benner-logo" alt="logo" /></div>
                    <div className='col-md-2 BENNER-MAIN-1'><img src={Google} className="Benner-logo" alt="logo" /></div>
                    <div className='col-md-2 BENNER-MAIN-1'><img src={Google} className="Benner-logo" alt="logo" /></div>

                </div>
                </div>
            </div>
        </>
    );
}

export default Benner;
