import './Logo.css';
import logo1 from './img/google.png'
function Logos() {
  return (
    <div className="Logos text-center">
       <h2>Astro is free, open source software made possible by these wonderful sponsors</h2>
       <div className='Logo1'>
        <img src={logo1} alt='tt' />
        <img src={logo1} alt='tt' />
       </div>
       <div className='Logo2'>
        <img src={logo1} alt='tt' />
        <img src={logo1} alt='tt' />
        <img src={logo1} alt='tt' />
        <img src={logo1} alt='tt' />
        <img src={logo1} alt='tt' />
        <img src={logo1} alt='tt' />
       </div>
       <div className='Logo3'>
       <img src={logo1} alt='tt' />
       </div>
    </div>
  );
}

export default Logos;
